// App.js

import React, { useState } from 'react';
import CourseList from './CourseList';
import CourseDetails from './CourseDetails';
import { Layout, Menu } from 'antd';
import { Carousel } from 'antd';
import './App.css';
import { Button} from 'antd';

const contentStyle = {
  margin: 0,
  height: '160px',
  color: '#fff',
  lineHeight: '160px',
  textAlign: 'center',
  // background: '#364d79',
};

const { Header, Content } = Layout;

const App = () => {
  // Sample courses data
  const [courses] = useState([
    {
      id: 1,
      image: 'https://s3-alpha.figma.com/hub/file/1166690750/85f69649-5387-44c2-ba45-81ae13812e36-cover.png',
      title: 'React Basics',
      description: 'Introduction to React fundamentals',
      duration: '200 days'
    },
    {
      id: 2,
      image: 'https://react.dev/images/home/conf2021/cover.svg',
      title: 'Advanced React',
      description: 'Advanced concepts in React development',
      duration: '100 days'
    },
    // Add more courses as needed
  ]);

  const [selectedCourse, setSelectedCourse] = useState(null);

  const handleViewDetails = (course) => {
    setSelectedCourse(course);
  };

  const handleCloseDetails = () => {
    setSelectedCourse(null);
  };
  const onChange = (currentSlide) => {
    console.log(currentSlide);
  }
  return (
    <div>
       <Layout>
        
       <Header style={{ background: '#fff', display: 'flex', justifyContent: 'space-between' }}>
          <div className="logo">
            <img src="https://programmingashram.in/assets/images/logo.png" alt="Logo" />
            {/* <h5>Pathsala</h5> */}
          </div>
          <Menu theme="light" mode="horizontal" defaultSelectedKeys={['1']} style={{ lineHeight: '64px' }}>
            <Menu.Item key="1">Learn</Menu.Item>
            <Menu.Item key="2">Mentors</Menu.Item>
          </Menu>
        </Header>
        <Content style={{ padding: '0 0px' }}>
          <div className="site-layout-content">
            <Carousel afterChange={onChange}>
              <div className='flexy gap-sm align-items-center'>
                  <div className='w-100'>
                    <img  src='https://img.freepik.com/free-vector/hand-drawn-picasso-style-illustration_23-2149577316.jpg' alt='' className='w-100 p-2 sliderImg' />
                  </div>
                  <div className='w-100 content-blk p-3'>
                    <h4>Skills that drive you forward</h4>
                    <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown.</p>
                    <div class='mt-2'>
                      <Button type="primary" size='large'>Get Started</Button>
                      <Button type="default" size='large' className='ms-2'>Mentors</Button>
                    </div>
                  </div>
                </div>
              <div>
                <h3 style={contentStyle}>2</h3>
              </div>
              <div>
                <h3 style={contentStyle}>3</h3>
              </div>
              <div>
                <h3 style={contentStyle}>4</h3>
              </div>
            </Carousel>

            <CourseList courses={courses} onViewDetails={handleViewDetails} />
            <CourseDetails course={selectedCourse} onClose={handleCloseDetails} />
          </div>
        </Content>
      </Layout>
    </div>
  );
};

export default App;
